<template>
    <div class="orders">
        <h1 class="title title--primary">Shipping Methods</h1>
        <p class="title title--secondary">New shipping method</p>
        <NewShippingMethodForm @submit="newShippingMethod($event)" />
        <h1 class="title title--primary">Orders</h1>
        <el-table
        :data="orders"
        striped>
            <el-table-column
            prop="date"
            label="Order placed"
            width="155px">
            </el-table-column>
            <el-table-column
            prop="shippingAddress"
            label="Ship to"
            width="200px">
            </el-table-column>
            <el-table-column
            prop="numOfItems"
            label="No. of items"
            width="130px">
            </el-table-column>
            <el-table-column
            label="Subtotal"
            width="130px">
                <template slot-scope="scope">
                    <span>£{{ scope.row.costs.subtotal }}</span>
                </template>
            </el-table-column>
            <el-table-column
            label="Shipping method"
            width="200px">
                <template slot-scope="scope">
                    <span>{{ scope.row.fulfillment.shippingMethod.name }} - £{{ scope.row.costs.shipping }}</span>
                </template>
            </el-table-column>
            <el-table-column
            label="Total paid"
            width="150px">
                <template slot-scope="scope">
                    <span>£{{ scope.row.costs.grandTotal }}</span>
                </template>
            </el-table-column>
            <el-table-column
            label="Payment"
            width="200px">
                <template slot-scope="scope">
                    <p style="margin: 4px 0;">
                        <span v-if="scope.row.payment.method === 'card' && scope.row.payment.paymentCard.last4">{{ scope.row.payment.paymentCard.last4 }}</span>
                        <span v-if="scope.row.payment.method === 'card'">Card - details unconfirmed</span>
                        <span v-else>Manual payment - cash or other</span>
                    </p>
                    <p class="payment-status" 
                    :class="{ pending: scope.row.payment.paymentStatus === 'pending', paid: scope.row.payment.paymentStatus === 'paid' }">
                        {{ scope.row.payment.paymentStatus }}
                    </p>
                </template>
            </el-table-column>
            <el-table-column
            label="Order status"
            width="200px">
                <template slot-scope="scope">
                    <span>{{ scope.row.fulfillment.orderStatus }}</span>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
            layout="prev, pager, next"
            @current-change="page = $event - 1"
            :hide-on-single-page=true
            :page-count="totalPages">
        </el-pagination>
    </div>
</template>

<script>
    import NewShippingMethodForm from "@/components/NewShippingMethodForm.vue"

    export default {
        name: "orders",
        components: {
            NewShippingMethodForm
        },
        data() {
            return {
                ordersRaw: [],
                activeNames: ["1"],
                page: 0,
                totalPages: null
            }
        },
        computed: {
            orders() {
                return this.ordersRaw.map((order) => {
                    const d = new Date(order.date)
                    order.date = d.getHours() + ":" + d.getMinutes() + " " + (d.getMonth() + 1) + '/' + d.getDate() + '/' +  d.getFullYear();
                    const a = order.fulfillment.shippingAddress
                    let firstLine = a.address
                    if(a.flatNumber) { firstLine = a.flatNumber + ", " + firstLine }
                    if(a.company) { firstLine = a.company + ", " + firstLine }
                    order.shippingAddress = a.firstName + " " + a.lastName + ", " + firstLine + ", " + a.postcode + ", " + a.city + ", " + a.country.name
                    order.numOfItems = order.items.reduce((accumulator, item) => {
                        return accumulator + item.quantity
                    }, 0)
                    return order
                })
            }
        },
        watch: {
            page() {
                this.getOrders();
            }
        },
        mounted() {
            this.$emit("set-loading")
            this.getOrders();
        },
        methods: {
            getOrders() {
                // add page query 
                let queryString = "?page=" + this.page
                // // add category filter query
                // this.selectedCategories.forEach((category) => {
                //     queryString = queryString + "&category=" + category
                // });
                // // add price range filter query
                // queryString = queryString + "&price=" + this.priceRange[0] + "&price=" + this.priceRange[1]
                // send request
                this.$axios.get("/orders" + queryString)
                .then((res) => {
                    console.log(res)
                    this.ordersRaw = res.data.orders;
                    this.totalPages = res.data.pages;
                    window.document.getElementsByClassName("el-main")[0].scroll(0,0)
                    this.$emit("stop-loading")
                })
                .catch(() => {
                    this.$message.error("Error loading orders. Please contact your system administrator")
                    this.$emit("stop-loading")
                })
            },
            newShippingMethod(data) {
                this.$axios.post("/orders/methods", data)
                .then(() => {
                    this.$message.success("Successfully created new shipping method")
                })
                .catch(() => {
                    this.$message.error("Error creating new shipping method. Please try again, then contact your system administrator if problem persists.")
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
    .payment-status {
        margin: 4px 40px;
        font-weight: 600;
        text-align: center;
        background-color: #aaa;
        color: #fff;
        &.pending {
            background-color: #d88c00;
        }
        &.paid {
            background-color: #18dc00
        }
    }
</style>